// BreadcrumbList Schema -   
export const getBreadcrumbSchema = (items: Array<{ name: string; url: string }>) => ({
  "@context": "https://schema.org",
  "@type": "BreadcrumbList",
  "itemListElement": items.map((item, index) => ({
    "@type": "ListItem",
    "position": index + 1,
    "name": item.name,
    "item": item.url
  }))
});

// FAQPage Schema -    (AI   )
export const getFAQSchema = (faqs: Array<{ question: string; answer: string }>) => ({
  "@context": "https://schema.org",
  "@type": "FAQPage",
  "mainEntity": faqs.map(faq => ({
    "@type": "Question",
    "name": faq.question,
    "acceptedAnswer": {
      "@type": "Answer",
      "text": faq.answer
    }
  }))
});

// MusicRecording Schema -   
export const getMusicRecordingSchema = (track: {
  name: string;
  description?: string;
  duration?: string;
  url?: string;
}) => ({
  "@context": "https://schema.org",
  "@type": "MusicRecording",
  "name": track.name,
  "description": track.description || "",
  "duration": track.duration || "",
  "url": track.url || "https://peaceandmusic.net/tracks",
  "byArtist": {
    "@type": "MusicGroup",
    "name": "    "
  }
});

// MusicPlaylist Schema -  
export const getMusicPlaylistSchema = (tracks: Array<{ name: string; url?: string }>) => ({
  "@context": "https://schema.org",
  "@type": "MusicPlaylist",
  "name": "  ",
  "description": "       ",
  "numTracks": tracks.length,
  "track": tracks.map(track => ({
    "@type": "MusicRecording",
    "name": track.name,
    "url": track.url || "https://peaceandmusic.net/tracks"
  }))
});

// ImageGallery Schema - 
export const getImageGallerySchema = (images: Array<{ url: string; caption?: string }>) => ({
  "@context": "https://schema.org",
  "@type": "ImageGallery",
  "name": "",
  "description": "       ",
  "image": images.map(img => ({
    "@type": "ImageObject",
    "url": img.url,
    "caption": img.caption || ""
  }))
});

// NewsArticle Schema -  
export const getNewsArticleSchema = (article: {
  headline: string;
  description: string;
  datePublished: string;
  url: string;
  imageUrl?: string;
}) => ({
  "@context": "https://schema.org",
  "@type": "NewsArticle",
  "headline": article.headline,
  "description": article.description,
  "datePublished": article.datePublished,
  "url": article.url,
  "image": article.imageUrl || "https://peaceandmusic.net/og-image.png",
  "author": {
    "@type": "Organization",
    "name": "    "
  }
});

